* {
  /* background-color: black; */
    box-sizing: border-box;
    
}
body {
  background-color: rgb(15, 15, 15);
  color: white;
  padding: 40px;
  font-family: Arial;
  text-align: center;
}

 .App {
     /* max-width: 420px; */
     /* max-width: 500px; */
     /* margin: 0 auto; */
     padding: 10px;
     /* float:right; */
     
}
.content {
    flex: 1 0 auto;
    padding: 10px;
  }
  .grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
}
#root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
.footer {
    flex-shrink: 0;
    text-align: center;
    background-color: rgb(15, 15, 15);
    color: white;
    font-weight: 200;
    height: auto;
  }

  .responsive {
    max-width: 100%;
    height: auto;
  }
p {
     text-align: center;
     font-style: normal;
     font-size: medium;
     font-family: Verdana;
     font-weight: 500;
}
label{
    font-size: medium;
    text-align: left;
    font-family: Verdana;
}
 input[type=text],input[type=email], select, textarea {
     width: 100%;
     padding: 12px;
     text-align: left;
     font-family: Verdana;
     border: 1px solid #ccc;
     border-radius: 4px;
     font-size: medium;
     box-sizing: border-box;
     margin-top: 6px;
     margin-bottom: 16px;
     resize: vertical;
}
 input[type=submit] {
     background-color: #4CAF50;
     color: white;
     font-family:Verdana;
     font-size: medium;
     padding: 12px 20px;
     border: none;
     border-radius: 4px;
     cursor: pointer;
}
 input[type=submit]:hover {
     background-color: #45a049;
     font-weight: bold;
     font-family: Verdana;
}


.column {
  
  width: 500px;
  display: inline-block;
  max-width: 600px;
 
  text-align: left;
  
  /* background-color: #ddd; */
  border-radius: 3px;
  margin: 15px;
  padding: 10px;
  height: auto; /* Should be removed. Only for demonstration */
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}

